*{font-family:'Inter', serif}body{margin:0;font-family:'Inter', serif;background-color:whitesmoke}.wrapper{max-width:1080px;margin:50px auto 0;background:#FFFFFF;box-shadow:0px 10px 20px rgba(0,0,0,0.04);border-radius:20px}.overlay{position:absolute;left:0;top:0;width:100%;height:100vw;background-color:rgba(0,0,0,0.5);z-index:1;opacity:1;transition:opacity 0.1s ease-out, visibility 0.1s ease-out}.drawer{position:absolute;right:0;top:0;width:385px;height:100vw;background-color:#FFFFFF;box-shadow:-10px 4px 24px rgba(0,0,0,0.1)}.drawer .empty_cart{margin:60px 0;width:285px;height:320px;padding:20px}.drawer .empty_cart p{font-style:normal;font-weight:normal;font-size:14px;line-height:24px;text-align:center}.drawer .empty_cart .backspase_button{width:245px;height:55px;border-radius:18px;background-color:#9DD458;padding:17px 50px 19px 31px;cursor:pointer}.drawer .empty_cart .backspase_button:hover{opacity:0.8}.drawer .empty_cart .backspase_button .backspase_button_text{color:#FFFFFF;font-style:normal;font-weight:300;font-size:16px;line-height:19px;text-align:center}.drawer .drawer_item{width:325px;height:119px;border:1px solid #F3F3F3;border-radius:20px;padding:30px 20px}.drawer .close_icon{cursor:pointer}.drawer h3{margin-bottom:8px;font-style:normal;font-weight:normal;font-size:14px;line-height:17px}.drawer .description{width:150px}.drawer .price{font-style:normal;font-weight:bold;font-size:14px;line-height:17px}.drawer .cost_descr{font-style:normal;font-weight:normal;font-size:16px;line-height:19px}.drawer .cost_price{font-style:normal;font-weight:600;font-size:16px;line-height:19px}.drawer .cart_divider{width:170px;border-bottom:1px dashed black;border-bottom-color:#DFDFDF}.drawer .cart_title{width:325px}.drawer .cart_button{background-color:#9DD458;width:325px;height:55px;border-radius:18px;color:#FFFFFF;text-align:center;padding:18px;cursor:pointer;transition:opacity 0.2s ease-in-out}.drawer .cart_button:hover{opacity:0.8}header{border-bottom:1px solid #EAEAEA}.headerLeft{display:flex;align-items:center}.headerLeft,h3,p{margin:0}.headerTitle{font-style:normal;font-weight:bold;font-size:20px;line-height:24px;text-transform:uppercase;color:#000000}.headerDescr{font-style:normal;font-weight:normal;font-size:14px;line-height:17px;color:#9D9D9D}.logo{width:40px;height:40px;margin-right:15px}.iconCart{margin-right:7px;cursor:pointer}.iconCart:hover{opacity:0.7}.iconHeart:hover{opacity:0.7}.iconUser:hover{opacity:0.7}.iconPlus:hover{opacity:0.6;box-shadow:0px 14px 30px rgba(0,0,0,0.02)}.content h1{font-style:normal;font-weight:bold;font-size:32px;line-height:39px;margin:0}.search_block{width:215px;height:40px;border:1px solid #F3F3F3;border-radius:10px;padding:10px;align-items:center}.search_block img{margin-right:10px}.search_block input{border:none}.items{width:210px;height:260px;border:1px solid #F3F3F3;border-radius:30px;padding-top:22px;padding-bottom:32px;padding-left:38px;padding-right:29px;transition:box-shadow 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;transition:box-shadow 0.25s ease-in-out, transform 0.25s ease-in-out;transition:box-shadow 0.25s ease-in-out, transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out}.items:hover{box-shadow:0px 14px 30px rgba(0,0,0,0.05);-webkit-transform:translateY(-5px);transform:translateY(-5px)}.items .items_descr{font-style:normal;font-weight:normal;font-size:14px;line-height:17px;margin-bottom:14px}.items .opacity-5{font-size:12px}.items .price{font-style:normal;font-weight:bold;font-size:14px;line-height:17px}.cards .cards_liked{position:absolute;cursor:pointer}.cards .cards_liked:hover{opacity:0.7;box-shadow:0px 14px 30px rgba(0,0,0,0.02)}.plus{cursor:pointer}

